import Vue from 'vue'
import Router from 'vue-router'
import ResumeIndex from '@/components/resume/Index'
import Styles from '@/components/Styles'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'ResumeIndex',
      component: ResumeIndex
    },
    {
      path: '/styles',
      name: 'Styles',
      component: Styles
    }
  ]
})



