<template>
  <div class="resume">
    <ResumeHeader :personalInfo="resume.personalInfo" class="mb-1"></ResumeHeader>

    <ResumeSelfDescription :selfDescription="resume.selfDescription" class="mb-1"></ResumeSelfDescription>

    <ResumeSkills :skills="resume.skills" class="mb-1"></ResumeSkills>

    <ResumeJobIntension :jobIntension="resume.jobIntension" class="mb-1"></ResumeJobIntension>

    <ResumeEducationExperience :educationExperiences="resume.educationExperiences" class="mb-1"></ResumeEducationExperience>

    <ResumeWorkExperience :workExperiences="resume.workExperiences" class="mb-1"></ResumeWorkExperience>

    <ResumeProjectExperience :projectExperiences="resume.projectExperiences" class="mb-1"></ResumeProjectExperience>

    <ResumeFooter></ResumeFooter>

  </div>
</template>

<script>
import yaml from 'js-yaml'
import ResumeHeader from '@/components/resume/Header'
import ResumeSelfDescription from '@/components/resume/SelfDescription'
import ResumeSkills from '@/components/resume/Skills'
import ResumeJobIntension from '@/components/resume/JobIntension'
import ResumeWorkExperience from '@/components/resume/WorkExperience'
import ResumeProjectExperience from '@/components/resume/ProjectExperience'
import ResumeEducationExperience from '@/components/resume/EducationExperience'
import ResumeFooter from '@/components/resume/Footer'

export default {
  name: 'ResumeIndex',
  data () {
    return {
      resume: {},
      flag: false
    }
  },
  mounted () {
    this.getResume()
  },
  methods: {
    getResume () {
      fetch('/static/resume.yml').then((response) => {
        response.text().then((text) => {
          let obj = yaml.safeLoad(text)
          this.resume = obj
        })
      })
    }
  },
  components: {
    ResumeHeader: ResumeHeader,
    ResumeSelfDescription: ResumeSelfDescription,
    ResumeSkills: ResumeSkills,
    ResumeJobIntension: ResumeJobIntension,
    ResumeWorkExperience: ResumeWorkExperience,
    ResumeProjectExperience: ResumeProjectExperience,
    ResumeEducationExperience: ResumeEducationExperience,
    ResumeFooter: ResumeFooter
  }
}
</script>

<style>
.resume{
  max-width: 700px;
  margin: 4rem auto;
}
</style>



