<template>
  <div class="resume-header" v-if="personalInfo">
    <div class="resume-header__top">
      <img :src="personalInfo.avatar" class="resume-header__avatar" alt="">
    </div>
    <div class="resume-header__down">
      <ul class="resume-header__personal-info list-unstyled">
        <li><strong>{{ personalInfo.name }}</strong></li>
        <li>{{ personalInfo.sex }}</li>
        <li>{{ personalInfo.education }}</li>
        <li>{{ personalInfo.location }}</li>
        <li>{{ personalInfo.born }}</li>
        <li>{{ personalInfo.workExperience }}</li>
        <li>{{ personalInfo.status }}</li>
      </ul>
      <ul class="resume-header__contact list-unstyled font-size-14">
        <li><i class="fa fa-envelope"></i> {{ personalInfo.contact.email }}</li>
        <li><i class="fa fa-phone"></i> {{ personalInfo.contact.phone }}</li>
      </ul>
      <ul class="resume-header__social-links list-unstyled">
        <li
          v-for="socialAccount in personalInfo.socialAccounts"
          :key="socialAccount.name"
        >
          <a :href="socialAccount.url" target="_blank"><i :class=" 'fa fa-'+socialAccount.name"></i></a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ResumeHeader',
  props: ['personalInfo']
}
</script>

<style lang="scss" scoped>
  .resume-header {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    .resume-header__top {
      background: url("../../assets/images/resum-header-bg.jpg") no-repeat center center;
      background-size: cover;
      position: relative;
      height: 9rem;
      padding: 1rem;
      text-align: center;
    }
    .resume-header__avatar {
      width: 120px;
      height: 120px;
      border-radius: 50%;
      position: absolute;
      bottom: -60px;
      transform: translate(-50%, 0);
    }
    .resume-header__down {
      padding: 4rem 1rem 1rem 1rem;
    }

    .resume-header__personal-info {
      display: flex;
      flex-direction: row;
      margin: 1rem 0;
      text-align: center;
      justify-content: center;
      li {
        padding: 0 8px;
      }
    }

    @media (max-width: 576px) {
      .resume-header__personal-info {
        flex-direction: column;
        li {
          margin-bottom: 5px;
        }
      }
    }
    .resume-header__contact{
      display: flex;
      flex-direction: row;
      margin: 1rem 0;
      text-align: center;
      justify-content: center;
      li {
        padding: 0 8px;
      }
    }

    @media (max-width: 576px) {
      .resume-header__contact {
        flex-direction: column;
        li {
          margin-bottom: 5px;
        }
      }
    }
    .resume-header__social-links {
      display: flex;
      flex-direction: row;
      text-align: center;
      justify-content: center;
      margin-bottom: 0;
      li {
        padding: 8px;
        font-size: 1.2rem;
      }
    }
  }
</style>



