<!-- CSS styles 可复用的CSS样式-->
<template>
  <div class="styles container mt-5">
    <h2 class="text-center">可复用的CSS样式</h2>

    <h4>vline</h4>
    <p>Lorem <em class="vline"></em> ipsum <em class="vline"></em> dolor</p>

    <h4>hline</h4>
    <div>Lorem ipsum dolor sit.</div>
    <hr class="hline">
    <div>Explicabo fugiat fugit odit?</div>
    <hr class="hline">
    <div>Aliquid dolores dolorum saepe?</div>
  </div>
</template>

<script>
export default {
  name: 'Styles'
}
</script>



